body {
  display: flex;
}
main {
  width: 100%;
}
.left-sidebar {
  width: 111px;
  border-right: 1px solid #f6f6f6;
}
.left-sidebar #logo-place {
  height: 96px;
}
.left-sidebar ul {
  padding: 0;
}
.left-sidebar ul li {
  display: block;
  width: 100%;
  height: 46px;
  text-align: center;
  line-height: 39px;
  vertical-align: middle;
  transition: background 0.3s ease-in-out;
  position: relative;
}
.left-sidebar ul li.current {
  background: #f9fbfc;
}
.left-sidebar ul li::after,
.left-sidebar ul li:after {
  position: absolute;
  content: '';
  display: block;
  width: 0px;
  border-bottom: 1px solid #34495e;
  bottom: 10px;
  left: 0;
  right: 0;
  margin: 0 auto;
  transition: width 0.1s ease-in-out;
}
.left-sidebar ul li.current::after,
.left-sidebar ul li.current:after,
.left-sidebar ul li:hover:after,
.left-sidebar ul li:hover::after {
  width: 84px;
}
.left-sidebar ul li a {
  display: block;
  height: 100%;
  color: #34495e;
}
.main-header {
  height: 71px;
  border-bottom: 1px solid #f6f6f6;
  background: #f9fbfc;
  display: table;
  width: 100%;
}
.main-header .search-block {
  height: 100%;
  display: table-cell;
  vertical-align: middle;
  width: 100%;
  padding-left: 88px;
}
.main-header .search-block div {
  position: relative;
}
.main-header .search-block div:after,
.main-header .search-block div::after {
  position: absolute;
  left: -34px;
  top: -3px;
  content: '';
  display: block;
  width: 25px;
  height: 24px;
  background: url(/public/images/sprite.png);
  background-position-y: -104px;
}
.main-header .search-block input[type="search"] {
  width: 100%;
  border: none;
  background: #f9fbfc;
  font-size: 14px;
  padding: 0;
  margin: 0;
}
.main-header .btn-block {
  width: 435px;
}
.main-header .btn-block > div {
  float: left;
  color: #34495e;
  padding: 0 17px;
  cursor: pointer;
  line-height: 70px;
  vertical-align: middle;
  position: relative;
  transition: background-color 0.2s ease-in-out;
}
.main-header .btn-block > div:before,
.main-header .btn-block > div::before {
  height: 41px;
  position: absolute;
  top: 14px;
  left: 0;
  bottom: 0;
  content: '';
  display: block;
  border-right: 1px solid #d2d7dc;
}
.main-header .btn-block > div:after,
.main-header .btn-block > div::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  content: '';
  height: 5px;
  background: transparent;
  transition: background-color 0.2s ease-in-out;
}
.main-header .btn-block > div.active {
  background: rgba(186, 205, 206, 0.102);
}
.main-header .btn-block > div.active:after,
.main-header .btn-block > div.active::after {
  background: rgba(52, 73, 94, 0.6);
}
.main-header .btn-block .icon-i {
  background: url(/public/images/sprite.png) no-repeat;
  float: left;
  margin-right: 18px;
}
.main-header .btn-block .add-order-i {
  width: 23px;
  height: 23px;
  margin-top: 24px;
}
.main-header .btn-block .filter-i {
  width: 23px;
  height: 20px;
  margin-top: 25px;
  background-position-y: -33px;
}
.main-header .btn-block .person-i {
  width: 33px;
  height: 33px;
  margin-top: 19px;
  background-position-y: -64px;
}
#content {
  padding: 28px;
}
#content .table-container {
  border: 1px solid rgba(52, 73, 94, 0.1);
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
  -webkit-border-top-left-radius: 10px;
  -webkit-border-top-right-radius: 10px;
}
#content .table-container table {
  width: 100%;
  border-spacing: 0;
  border-color: rgba(52, 73, 94, 0.1);
  border-collapse: collapse;
  border: none;
}
#content .table-container table thead {
  color: #808b96;
  font-size: 10px;
  text-transform: uppercase;
}
#content .table-container table thead tr,
#content .table-container table thead td {
  border-top: none;
}
#content .table-container table tr:last-child td {
  border-bottom: none;
}
#content .table-container table tr td {
  padding: 10px;
  border-color: rgba(52, 73, 94, 0.1);
}
#content .table-container table tr td:first-child {
  border-left: none;
}
#content .table-container table tr td:last-child {
  border-right: none;
}
#content .new-order-section .client-section,
#content .edit-order-section .client-section,
#content .new-order-section .device-section,
#content .edit-order-section .device-section {
  width: 50%;
  float: left;
}
#content .new-order-section .parts-section {
  margin-bottom: 30px;
}
#content .new-order-section .parts-section .add-part-fields {
  margin-bottom: 10px;
}
#content .new-order-section .parts-section .name-field,
#content .new-order-section .parts-section .number-field,
#content .new-order-section .parts-section .icost-field,
#content .new-order-section .parts-section .ocost-field,
#content .new-order-section .parts-section .add-field {
  float: left;
  padding-right: 10px;
}
#content .new-order-section .parts-section .name-field {
  width: 30%;
}
#content .new-order-section .parts-section .number-field,
#content .new-order-section .parts-section .icost-field,
#content .new-order-section .parts-section .ocost-field {
  width: 22%;
}
#content .new-order-section .parts-section .number-field input,
#content .new-order-section .parts-section .icost-field input,
#content .new-order-section .parts-section .ocost-field input {
  text-align: right;
}
#content .new-order-section .parts-section .add-field {
  width: 4%;
  padding-right: 0;
}
#content .new-order-section .parts-section input {
  margin-bottom: 0;
}
#content .new-order-section .jobs-section {
  margin-bottom: 30px;
}
#content .new-order-section .jobs-section .add-job-fields {
  margin-bottom: 10px;
}
#content .new-order-section .jobs-section .name-field,
#content .new-order-section .jobs-section .number-field,
#content .new-order-section .jobs-section .cost-field,
#content .new-order-section .jobs-section .add-field {
  float: left;
  padding-right: 10px;
}
#content .new-order-section .jobs-section .name-field,
#content .new-order-section .jobs-section .number-field,
#content .new-order-section .jobs-section .cost-field {
  width: 32%;
}
#content .new-order-section .jobs-section .number-field input,
#content .new-order-section .jobs-section .cost-field input {
  text-align: right;
}
#content .new-order-section .jobs-section .add-field {
  width: 3%;
  padding-right: 0;
}
#content .new-order-section .jobs-section input {
  margin-bottom: 0;
}
#content .new-order-section .add-field {
  padding-top: 15px;
  padding-left: 15px;
}
#content .new-order-section .add-field i {
  display: block;
  width: 25px;
  height: 25px;
  background: url(/public/images/sprite.png) no-repeat;
  background-position-y: -135px;
}
#content .pagination {
  padding: 0;
  margin: 25px 0;
}
#content .pagination li {
  display: block;
  float: left;
  height: 30px;
  width: 40px;
  border: 1px solid rgba(52, 73, 94, 0.2);
  color: #2c3e50;
  padding-top: 4px;
  margin-right: 2px;
}
#content .pagination li:first-child {
  padding-top: 2px;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  -webkit-border-bottom-left-radius: 5px;
  -webkit-border-top-left-radius: 5px;
}
#content .pagination li:last-child {
  padding-top: 2px;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  -webkit-border-bottom-right-radius: 5px;
  -webkit-border-top-right-radius: 5px;
}
#content .pagination li a {
  color: #2c3e50;
  display: block;
  text-align: center;
}
#content .pagination li.disable {
  border: 1px solid rgba(249, 251, 252, 0.8);
}
#notifications {
  position: fixed;
  top: 20px;
  width: 80%;
  z-index: 20;
}
#notifications div.notification {
  display: table;
  margin: 10px auto;
  padding: 30px;
  font-size: 16px;
  background: blue;
  color: #fff;
}
